    top: 0;
  }

  [role="banner"] {
    padding-top: 200px;
    padding-bottom: 3em;
  }

  [role="banner"] h1 {
    font-size: 4.8rem;
    letter-spacing: 0;
    line-height: 0.8;
  }
  [role="banner"] h1:after {
    position: absolute;
    top: 15.7em;
    right: 30%;
  }

  [role="banner"] h2 {
