  }

  .summary p:nth-child(2) {
    background: yellow;
    position: relative;
    visibility: hidden;
    top: 2em;
    font-size: 1.4rem;
  }

  .summary p:nth-child(2):before {
    content: "";
    display: block;
    visibility: visible;
    position: absolute;
    right: 21em;
    top: -0.25em;
    background: transparent url(http://www.csszengarden.com/220/s/czg.svg)
      no-repeat -159px -1px;
    width: 6em;
    height: 30px;
  }

  .summary p:nth-child(2):after {
    content: "";
    display: block;
    visibility: visible;
    position: absolute;
    right: 14em;
    top: -0.25em;
    background: transparent url(http://www.csszengarden.com/220/s/czg.svg)
      no-repeat -277px 0;
    width: 6em;
    height: 30px;
  }

  .summary p:nth-child(2) a {
    visibility: visible;
    background: transparent url(http://www.csszengarden.com/220/s/czg.svg)
      no-repeat -376px -93px;
    width: 6em;
    height: 25px;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    display: block;
    position: absolute;
    top: 0;
    right: 7em;
  }
  .summary p:nth-child(2) a:hover,
  .summary p:nth-child(2) a:focus {
    background-position: -396px -3px;
    text-indent: 0;
    background: none;
  }

  .summary p:nth-child(2) a[href*="css"] {
    background-position: -472px -94px;
    right: 0;
  }
  .summary p:nth-child(2) a[href*="css"]:hover,
  .summary p:nth-child(2) a[href*="css"]:focus {
    background-position: -512px -4px;
  }

  .preamble {
    padding-top: 10em;
  }
  .preamble h3 {
    top: 5.9em;
  }

  .design-selection li {
    width: 23%;
    margin-bottom: 3em;
    padding-right: 2%;
  }
  .design-selection li:nth-child(odd) {
    width: 23%;
    clear: none;
    padding-right: 2%;
  }

  .next {
    margin-right: 2em;
    margin-left: -0.4em;
  }
  .viewall {
    margin-left: 0;
  }

  footer {
    padding-bottom: 3em;
    margin-top: -5em;
  }
}

@media screen and (min-width: 1130px) {
  [role="article"],
  [role="banner"] {
    padding-left: 25%;
  }

  .design-selection {
    position: absolute;
    left: 5%;
    top: 30em;
  }
  .design-selection ul {
    margin: 0;
    width: 100%;
  }
  .design-selection li {
    float: none;
    width: 100% !important;
    padding: 0 !important;
    margin-bottom: 2em;
  }
  .select:after {
    text-align: left;
    background-position: 0 bottom;
    padding: 0 0 2em;
    margin: 0 0 2em;
  }

  .design-archives {
    position: absolute;
    left: 5%;
    top: 95em;
  }
}

@media screen and (min-width: 1130px) and (min-height: 1037px) {
  .design-selection,
  .design-archives {
    position: fixed;
  }
}

/*-------------------------------------------    
	Animations
-------------------------------------------*/
@-webkit-keyframes FADEY {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.intro {
  -webkit-animation-name: FADEY;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: 1;
}

[role="article"] {
  -webkit-opacity: 0;
  -webkit-animation-name: FADEY;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0.5s;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
}

.design-selection,
.design-archives {
  -webkit-opacity: 0;
  -webkit-animation-name: FADEY;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
  -webkit-animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
}

/*-------------------------------------------    
	IE, i.e. Internet Explorer (heh)
-------------------------------------------*/

@media screen and (min-width: 0\0) {
  /* IE9 and IE10 rule sets go here */

  .main h3,
  .preamble h3,
  .select {
    visibility: visible;
  }
  [role="banner"] h1 {
    visibility: visible;
    width: auto;
    display: block;
  }

  .main h3:after,
  .preamble h3:after,
  .select:after {
    display: none;
  }
  [role="banner"] h1:before {
    display: none;
  }
}

/* IE8 and below */
.main h3,
.preamble h3,
.select {
  visibility: visible\9;
  font-size: 20px\9;
}
.main h3:after,
.preamble h3:after,
.select:after {
  display: none\9;
}

.page-wrapper {
  width: 800px\9;
}
.preamble {
  padding-top: 50px\9;
  margin-top: 50px\9;
}

h1 {
  font-size: 48px\9;
  text-transform: uppercase\9;
  margin: 0\9;
  padding-top: 30px\9;
}
h2 {
  font-size: 20px\9;
}

.next a,
.previous a,
.viewall a,
.zen-resources a {
  background-image: url(http://www.csszengarden.com/220/i/czg.png) \9;
}

/* IE7 & below */
.next a,
.previous a,
.viewall a,
.zen-resources a {
  *background: none;
  *width: auto;
  *height: auto;
  *display: block;
  *text-indent: 0;
  *white-space: normal;
  *overflow: visible;
}

.design-archives {
  *position: static;
  *margin-top: 30px;
}

.design-archives li {
  *display: inline;
  *margin-right: 20px;
}

.next {
  *margin-right: 2em;
  *margin-left: -0.4em;
}

.viewall {
  *margin-left: 0;
}
